  background: #fff;
  position: relative;
  margin-top: -90px;
  -webkit-box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 0 0 10px 0px rgba(0, 0, 0, 0.1);
}
.trip-form h3 {
  font-size: 20px;
  margin-bottom: 30px;
  color: #000;
}

.item-1 {
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  background: #fff;
}
.item-1 .item-1-contents {
  padding: 30px;
  border: 1px solid #ccc;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top: none;
  position: relative;
}
.item-1 .item-1-contents ul {
  list-style: none;
  display: block;
  margin: 0;
  padding: 0;
}
.item-1 .item-1-contents ul li {
  list-style: none;
  margin: 0 0 10px 0;
  padding: 0;
  position: relative;
}
.item-1 .item-1-contents ul li .price {
  -webkit-box-flex: 0;
  -ms-flex: 0 0 90px;
  flex: 0 0 90px;
  text-align: right;
}
.item-1 .item-1-contents h3 {
  position: relative;
  color: #fd4d40;
  padding-bottom: 30px;
  margin-bottom: 30px;
}
.item-1 .item-1-contents h3:after {
  left: 0;
  bottom: 0;
  content: "";
  width: 40px;
  height: 2px;
  background: #fd4d40;
  position: absolute;
}

.how-it-works .step {
  width: 20%;
  display: inline-block;
  text-align: center;
  position: relative;
}
.how-it-works .step:after {
  width: 100%;
  height: 4px;
  content: "";
  position: absolute;
  z-index: -1;
  top: 25px;
  background: #fd4d40;
}
.how-it-works .step:last-child:after {
  display: none;
}
.how-it-works .step .number {
  z-index: 1;
  width: 50px;
  height: 50px;
  border: 4px solid #fd4d40;
  background: #fff;
  border-radius: 50%;
  color: #fd4d40;
  display: block;
  position: relative;
  margin: 0 auto 10px auto;
}
.how-it-works .step .number > span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font-size: 1.3rem;
  color: #fd4d40;
}
.how-it-works .step .caption {
  display: block;
}

.service-1 {
  text-align: center;
}
.service-1 .service-1-icon {
  width: 90px;
  height: 90px;
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
  border: 4px solid #fd4d40;
  border-radius: 50%;
  color: #fff;
}
.service-1 .service-1-icon > span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font-size: 3rem;
}
.service-1 .service-1-contents h3,
.service-1 .service-1-contents p {
  color: #fff;
}
.service-1 .service-1-contents p {
  color: rgba(255, 255, 255, 0.5);
}
.service-1 .service-1-contents h3 {
  margin-bottom: 10px;
  font-size: 20px;
}
.service-1.dark .service-1-contents h3,
.service-1.dark .service-1-contents p {
  color: #000;
}
.service-1.dark .service-1-contents h3 {
  margin-bottom: 10px;
  font-size: 20px;
}
.service-1.dark .service-1-contents h3,
.service-1.dark .service-1-contents p {
  color: #000;
}

.section-3 {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}
.section-3 > .container {
  z-index: 2;
}
.section-3:before {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  background-color: rgba(0, 0, 0, 0.5);
}

.testimonial-2 {
  background: #fff;
  padding: 30px;
  -webkit-box-shadow: 0 5px 30px -5px rgba(0, 0, 0, 0.1);
  box-shadow: 0 5px 30px -5px rgba(0, 0, 0, 0.1);
}
.testimonial-2 blockquote {
  font-size: 18px;
}
.testimonial-2 .v-card img {
  width: 50px;
  margin: 0 auto;
  -webkit-box-flex: 0;
  -ms-flex: 0 0 50px;
  flex: 0 0 50px;
  border-radius: 50%;
}

/* barberz */
.section-heading {
  position: relative;
  padding-bottom: 30px;
  margin-bottom: 30px;
  font-size: 2.5rem;
  color: #fd4d40;
}
@media (max-width: 991.98px) {
  .section-heading {
    font-size: 2rem;
  }
}
.section-heading:after {
  width: 70px;
  height: 2px;
  background: #fd4d40;
  bottom: 0;
  left: 0;
  font-size: 22px;
  content: "";
  position: absolute;
  color: #ccc;
}
.section-heading.primary-color-icon:after {
  color: #fd4d40;
}
.section-heading.text-center:after {
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
}

.nonloop-block-13 .owl-nav {
  display: none;
}

.nonloop-block-13 .owl-stage {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.nonloop-block-13 .owl-stage .owl-item {
  height: 100% !important;
}

.nonloop-block-13 .owl-dots {
  display: block;
  text-align: center;
  position: relative;
  position: absolute;
  top: 0;
  right: 0;
}
.nonloop-block-13 .owl-dots .owl-dot {
  display: inline-block;
}
.nonloop-block-13 .owl-dots .owl-dot > span {
  margin: 5px;
  width: 7px;
  height: 7px;
  background-color: rgba(255, 255, 255, 0.3);
  display: inline-block;
  border-radius: 50%;
}
.nonloop-block-13 .owl-dots .owl-dot.active > span {
  background: #fff;
}

.img-years {
  position: relative;
  display: block;
}
.img-years .year {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #fd4d40;
  position: absolute;
  right: 0;
  bottom: 0;
  -webkit-transform: translate(50%, 50%);
  -ms-transform: translate(50%, 50%);
  transform: translate(50%, 50%);
}
@media (max-width: 991.98px) {
  .img-years .year {
    position: relative;
    -webkit-transform: translate(0%, 0%);
    -ms-transform: translate(0%, 0%);
    transform: translate(0%, 0%);
    margin: -30px auto 0 auto;
  }
}
.img-years .year > span {
  width: 100%;
  color: #fff;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 3rem;
  display: block;
  line-height: 1;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.img-years .year > span > span {
  line-height: 1.2;
  display: block;
  font-size: 0.8rem;
}

/* Noxen */
.site-info {
  line-height: 1.2;
  color: rgb(255, 255, 255);
  font-size: 0.9rem;
}
.site-info .block-icon {
  font-size: 2rem;
  color: #fd4d40;
}

.top-social a {
  display: inline-block;
  padding: 20px;
}

.menu-wrap {
  padding: 10px 20px;
  background: #fff;
  border-radius: 20px;
}

.subtitle {
  font-size: 13px;
  text-transform: uppercase;
  color: #ccc;
}

.feature-1 {
  padding: 30px;
  background: #edf7ff;
}
.feature-1 h3 {
  font-size: 18px;
}
.feature-1 p {
  font-size: 14px;
  color: #9aa3a6;
}
.feature-1 .wrap-icon {
  font-size: 2rem;
}

.sign img {
  width: 200px;
}

.counter-wrap {
  background: #222;
}

.counter .wrap-icon {
  font-size: 3.2rem;
  color: #fff;
}

.counter .text {
  line-height: 1;
}
.counter .text .number {
  font-size: 2rem;
  color: #ffd692;
}
.counter .text .caption {
  color: rgba(255, 255, 255, 0.7);
}

.white-dots {
  position: relative;
}
.white-dots:before {
  position: absolute;
  content: "";
  z-index: -1;
  background-repeat: repeat;
  top: -50px;
  left: -50px;
  width: 100%;
  height: 100%;
}

.block-2 {
  padding: 40px;
  position: relative;
  text-align: center;
}
.block-2 .wrap-icon {
  position: relative;
  width: 80px;
  margin-left: auto;
  margin-right: auto;
  height: 80px;
  display: block;
  border-radius: 50%;
  margin-top: -70px;
  margin-bottom: 30px;
}
.block-2 .wrap-icon > span {
  color: #fff;
  font-size: 2rem;
  text-align: center;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.block-2 h2 {
  font-size: 18px;
  color: #fff;
}
.block-2 p {
  color: rgba(255, 255, 255, 0.7);
}
.block-2.red,
.block-2.red .wrap-icon {
  background-color: #fd4d40;
}
.block-2.yellow,
.block-2.yellow .wrap-icon {
  background-color: #fdb62f;
}
.block-2.teal,
.block-2.teal .wrap-icon {
  background-color: #16c3b0;
}

.text-red {
  color: #fd4d40;
}

.text-yellow {
